import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom'
import './index.scss';
import loadable from '@loadable/component';
import { Menu } from 'element-react';
import { fromJS, is } from 'immutable';
const Guide = loadable(() => import('../guide'))

class App extends Component {
	state = {
		defaultActive: '',
		sidebarList: [
			{
				title: 'Basic',
				list: [
					{ title: 'Color 色彩', url: '/basic/color' },
					{ title: 'Icon 图标', url: '/basic/icon' },
					{ title: 'Button 按钮', url: '/basic/button' }
				]
			},
			{
				title: 'Form',
				list: [
					{ title: 'Radio 单选框', url: '/form/radio' },
					{ title: 'Checkbox 多选框', url: '/form/checkbox' },
					{ title: 'Input 输入框', url: '/form/input' },
					{ title: 'InputNumber 计数器', url: '/form/inputNumber' },
					{ title: 'Select 选择器', url: '/form/select' },
					{ title: 'Switch 开关', url: '/form/switch' },
					{ title: 'Slider 滑块', url: '/form/slider' },
					{ title: 'TimePicker 时间选择器', url: '/form/timePicker' },
					{ title: 'DatePicker 日期选择器', url: '/form/datePicker' },
					{ title: 'DateTimePicker 日期时间选择器', url: '/form/dateTimePicker' },
					{ title: 'Upload 上传', url: '/form/upload' },
					{ title: 'Rate 评分', url: '/form/rate' },
					{ title: 'ColorPicker 颜色选择器', url: '/form/colorPicker' }
				]
			},
			{
				title: 'Data',
				list: [
					{ title: 'Table 表格', url: '/data/table' },
					{ title: 'Tag 标签', url: '/data/tag' },
					{ title: 'Progress 进度条', url: '/data/progress' },
					{ title: 'Pagination 分页', url: '/data/pagination' },
					{ title: 'Badge 标记', url: '/data/badge' }
				]
			},
			{
				title: 'Notice',
				list: [
					{ title: 'Alert 警告', url: '/notice/alert' },
					{ title: 'Loading 加载', url: '/notice/loading' },
					{ title: 'Message 消息提示', url: '/notice/message' },
					{ title: 'MessageBox 弹框', url: '/notice/messageBox' },
					{ title: 'Notification 通知', url: '/notice/notification' }
				]
			},
			{
				title: 'Navigation',
				list: [
					{ title: 'NavMenu 导航菜单', url: '/navigation/navMenu' },
					{ title: 'Tabs 标签页', url: '/navigation/tabs' },
					{ title: 'Breadcrumb 面包屑', url: '/navigation/breadcrumb' },
					{ title: 'Dropdown 下拉菜单', url: '/navigation/dropdown' },
					{ title: 'Steps 步骤条', url: '/navigation/steps' }
				]
			},
			{
				title: 'Others',
				list: [
					{ title: 'Dialog 对话框', url: '/others/dialog' },
					{ title: 'Tooltip 文字提示', url: '/others/tooltip' },
					{ title: 'Popover 弹出框', url: '/others/popover' },
					{ title: 'Card 卡片', url: '/others/card' },
					{ title: 'Timeline 时间线', url: '/others/timeline' },
					{ title: 'Backtop 回到顶部', url: '/others/backtop' }
				]
			}
		]
	}

	componentDidMount() {
		this.resetActivePath(this.props.location.pathname)
		this.props.history.listen((route) => {
			this.resetActivePath(route.pathname)
		})
	}

	componentWillReceiveProps(nextProps) {
	}	

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}
	
	componentDidUpdate() {

	}

	componentWillUnmount() {
		this.setState = (state, callback) => {
			return;
		};
	}

	resetActivePath = async (pathname) => {
		var pathname = pathname || this.props.location.pathname
		let list = this.state.sidebarList
		let fundpath = false
		label:
		for (var i = 0, len = list.length; i < len; i++) {
			for (var j = 0, length = list[i].list.length; j < length; j++) {
				if (pathname.endsWith(list[i].list[j].url)) {
					fundpath = true
					await this.setState({
						defaultActive: i + '-' + j
					})
					break label;
				}
			}
		}

		if (fundpath) return
		await this.setState({
			defaultActive: '1'
		})
	}

	onSelect = async (index) => {
		await this.setState({
			defaultActive: index
		})
		if (index == '1') return this.props.history.push('/component'); 
		let arr = index.split('-');
		await this.setState({
			defaultActive: arr[0] + '-' + arr[1]
		})
		let url = this.state.sidebarList[arr[0]].list[arr[1]].url;
		this.props.history.push('/component' + url);
	}

	thisComponent = () =>{
		var pathname = this.props.location.pathname
		pathname = pathname.substring(1)
		if (pathname == 'component') return Guide;
		let arr = pathname.split('/')
		arr.shift()
		var path = arr.join('/')
		let list = this.state.sidebarList
		for (let i = 0, len = list.length; i < len; i++) {
			for (let j = 0, length = list[i].list.length; j < length; j++) {
				if ('/' + path == list[i].list[j].url) {
					return loadable(() => import('../' + path))
				}
			}
		}
		return Guide
	}

	render() {
		let { defaultActive, sidebarList } = this.state;
		return (
			<div id="component" className="flex">
				<Menu mode="vertical" className="sidebar pb-20" onSelect={this.onSelect}>
					<Menu.Item index="1" className={defaultActive == '1' ? 'active' : null}>开发指南</Menu.Item>
					{
						sidebarList.map((val, idx) => {
							return (
								<Menu.ItemGroup title={val.title} key={idx}>
									{
										val.list.map((value, index) => {
											return <Menu.Item className={defaultActive == idx + '-' + index ? 'active' : null} 
												index={idx + '-' + index} key={index}>{value.title}</Menu.Item>
										})
									}
								</Menu.ItemGroup>
							)
						})
					}
				</Menu>

				<div className="right">
					<Switch>
						<Route path={this.props.location.pathname} exact component={this.thisComponent()}/>
					</Switch>
				</div>
			</div>
		);
	}
}

export default App;
